<template>
  <div class="security-table">
    <div class="ibox-content">
      <table>
        <thead>
        <tr>
          <th>日期</th>
          <th>时间</th>
          <th>容器ID</th>
          <th>容器名称</th>
          <th>告警等级</th>
          <th>规则</th>
          <th>事件描述</th>
          <th>主机名</th>
          <th>操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in queryResult.hits" :key="item.id">
          <td>{{(new Date(item._source['@timestamp'])).toLocaleString()}}</td>
          <td>{{(new Date(item._source.time)).toLocaleString()}}</td>
          <td>{{item._source.output_fields['container.id']}}</td>
          <td>{{item._source.output_fields['container.name']}}</td>
          <td><i class="icon-font icon-red">&#xe603;</i> {{item._source.priority}}</td>
          <td>{{item._source.rule}}</td>
          <td>
            <el-tooltip class="item" effect="dark" placement="bottom">
              <div slot="content">{{item._source.output}}</div>
              <p class="item-description">{{item._source.output}}</p>
            </el-tooltip>
          </td>
          <td>{{item._source.beat.hostname}}</td>
          <td><i class="icon-font icon-bottom" @click="handleShow">
            查看详情 &#xe602;</i></td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">

  export default {
    props: ['queryResult'],
    data() {
      return {}
    },
    methods: {
      handleShow() {
        this.$emit('handleShowAside', 'true')
      }
    },
    created() {
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .security-table
    margin: 30px 0 200px 0
    .ibox-content
      text-align: center
      table
        width: 100%
        border-collapse: collapse
        margin-bottom: 40px
        thead
          tr
            background #F7F7F7
            height 50px
            box-shadow 4px 4px 20px #FAFAFA
            th
              font-size 1.5em
              font-weight bold
              color #525252
            th:last-child
              display inline-block
              margin 17px -50px 0 0
        tbody
          tr
            font-size: 1.4em
            color #525252
            height 60px
            td:last-child :hover
              color #3E7AF6
              text-decoration underline

  tr:hover
    background #FAFAFA

  .icon-bottom
    color #548AF8

  .icon-red
    color #FF0000
    font-size 0.7em
</style>
<style lang="stylus">
  .el-tooltip__popper
    max-width 400px
    line-height 180%

  .item-description
    width 100px
    height 20px
    overflow hidden
    white-space nowrap
    text-overflow ellipsis
</style>



