<template>
  <div>
    <header>
      <navbar>
      </navbar>
    </header>
    <section class="main">
      <p class="main-nav">
        <router-link to="/">
          总览
        </router-link>
        <span>> >警告信息详情</span>
      </p>
      <section class="main-wrapper">
        <div class="brief">
          <el-container>
            <transition name="fade">
              <el-aside width="400px" v-if="show">
                <p>警报等级分布图</p>
                <div class="rankedDistrbution">
                  <h5>49,616(总警报数)</h5>
                  紧急
                  <el-progress :text-inside="true"
                               :stroke-width="12" :percentage="70" color="#FF0000"></el-progress>
                  警告
                  <el-progress :text-inside="true"
                               :stroke-width="12" :percentage="70" color="#FF6347"></el-progress>
                  严重
                  <el-progress :text-inside="true"
                               :stroke-width="12" :percentage="70" color="#FFBBFF"></el-progress>
                  一般
                  <el-progress :text-inside="true"
                               :stroke-width="12" :percentage="70" color="#FFFF00"></el-progress>
                  提示
                  <el-progress :text-inside="true"
                               :stroke-width="12" :percentage="70" color="#FFEFDB"></el-progress>
                </div>
                <button class="icon-shink" @click="handleClick">
                  <img :src="imgAddress"></button>
                <p class="fontLocation">事件详情</p>
                <div class="outputRange">
                  <div class="errorMsg"><span>Error:</span>
                    <a>File below a known binary directory
                      opened for writing (user=root command=touch /bin/hack
                      file=/bin/hack)"</a></div>
                  <div class="errorMsg">
                    <span><i>*</i> user.name :</span>
                    <a>"root"</a></div>
                  <div class="errorMsg"><span><i>*</i> evt.time :</span>
                    <a>"1507591916746609046"</a></div>
                  <div class="errorMsg"><span><i>*</i> fd.name :</span>
                    <a>"/bin/hack"</a></div>
                  <div class="errorMsg"><span><i>*</i> proc.cmdline :</span>
                    <a>"touch /bin/hack"</a></div>
                </div>
              </el-aside>
            </transition>
            <el-main>
              <p>报警输出JSON对象列表</p>
              <div class="alertList">
                <span><i class="iconfont icon-screen">&#xe60d;
                </i>
                <i> 查询日期：</i></span>
                <button class="otherDay">
                  今天
                </button>
                <button class="yesterday">
                  昨天
                </button>
                <button class="otherDay">
                  近7天
                </button>
                <button class="otherDay">
                  近30天
                </button>
                <el-date-picker
                  @change="getDateArray"
                  v-model="dateArray"
                  type="daterange"
                  range-separator="-"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                  size="mini"
                  style="width: 220px;">
                </el-date-picker>
              </div>
              <div class="securitylists-text">
                <p class="security-search-box">
                  <el-select v-model="must[1].field" style="width: 108px" placeholder="请选择">
                    <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                  <input class="search-text" type="text"
                         placeholder="请输入搜索JSON对象" v-model="must[1].value">
                  <button @click="handleSearch" class="search-btn btn">
                    <i class="iconfont icon-search">&#xe665;
                    </i></button>
                </p>
              </div>
              <alert-table @handleShowAside="handleShow" :queryResult="querydata"></alert-table>
              <div class="divblock">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-sizes="[5, 10, 20, 40]"
                  :page-size="100"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="querydata.total"
                  background>
                </el-pagination>
              </div>
            </el-main>
          </el-container>
        </div>
      </section>
    </section>
  </div>
</template>
<script type="text/ecmascript-6">
  import axios from 'axios'
  import Navbar from 'components/navbar'
  import AlertTable from './components/alertTable'
  import imgAdd from './img/translate.png'

  export default {
    components: {
      Navbar,
      AlertTable
    },
    name: 'index',
    data() {
      return {
        activeIndex: '1',
        dateArray: '',
        imgAddress: imgAdd,
        show: true,
        options: [{
          value: '@timestamp',
          label: '日期'
        }, {
          value: 'time',
          label: '时间'
        }, {
          value: 'container.id',
          label: '容器ID'
        }, {
          value: 'container.name',
          label: '容器名称'
        }, {
          value: 'priority',
          label: '告警等级'
        }, {
          value: 'rule',
          label: '规则'
        }, {
          value: 'output ',
          label: '事件描述'
        }, {
          value: 'hostName',
          label: '主机名'
        }],
        value: '',
        must: [
          {
            type: 'range',
            from: '2015-01-01T12:10:30Z',
            to: '2018-10-19T12:10:30Z'
          },
          {
            type: 'match',
            field: 'rule',
            value: 'shell'
          }
        ],
        pagination: {
          from: 0,
          size: 5
        },
        content: '',
        querydata: '',
        currentPage: ''
      }
    },
    methods: {
      getDateArray() {
        this.must[0].from = this.dateArray[0]
        this.must[0].to = this.dateArray[1]
      },
      handleClick() {
        this.show = false
      },
      handleShow(data) {
        this.show = data
      },
      handleSearch() {
        axios.post('http://192.168.3.67:8000/defensemonitor', {
          must: this.must,
          pagination: this.pagination
        }).then(res => {
          console.log(res)
          this.querydata = res.data
        })
      },
      handleSizeChange(val) {
        this.pagination.size = val
        this.handleSearch()
        // console.log('每页 ' + this.pagination.size + '条')
      },
      handleCurrentChange(val) {
        this.currentPage = val
        this.pagination.from = (this.currentPage - 1) * this.pagination.size
        // console.log('当前页:' + this.currentPage)
        this.handleSearch()
      }
    },
    created() {
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .main
    width 88%
    margin 0 auto
    position relative
    top 58px
    .main-nav
      font-size 1.6em
      color #525252
      padding 50px 0 25px 15px
      border-bottom #E5E5E5 solid 1.2px
      a
        color #4470E9
    .brief
      width 91%
      margin 0 auto
      padding 0 0 25px 0
      p
        font-size 1.6em
        color #333333
        font-weight 600
      span
        font-size 14px
        color #4D4D4D
        margin 30px 0 20px 0
        i
          color #808080
          padding 0px 2px 5px 0
      .alertList
        button
          line-height: 30px
          padding 0 15px
          margin 7.5px
      i
        display: inline-block
      .yesterday
        color #FFFFFF
        background #6F97E8
        border-color snow
      .otherDay
        color #3E7AF6
        background #F4F7FE
        border-color snow

  .el-header, .el-footer
    background-color #B3C0D1
    color #333
    text-align center
    line-height 60px

  .el-container
    margin-top 35px

  .el-aside
    background-color #FAFAFA
    color #333
    line-height 25px
    padding 30px 0 0 20px
    .icon-shink
      margin-left 347px
      background-color #fafafa
      img
        margin-left: 6px;

  .el-main
    background-color #fff
    color #333
    line-height 25px

  .rankedDistrbution
    background-color #fff
    margin 25px 25px 20px 0
    padding 28px 40px 30px 20px

  .icon-screen
    color #5286F7
    font-size: 1.5em

  .icon-search
    color #fff
    font-size: 1.5em

  .alertList
    margin 34px 0 00px 0

  .securitylists-text
    margin-bottom: 30px
    p
      display inline-block
      .search-text
        width: 300px
        height: 30px
        border: #5286F7 1px solid
        color: #808080
        padding: 0 0 0 15px
        font-size: 14px
        margin: 30px 0 0 0
        display: inline-block
      .search-btn
        height 32px
        width 50px
        background: #3E7AF6
        border: 0px
        color: #FFFFFF
        font-size: 14px
        line-height: 32px
        margin: 0px 0 0 -5px

  .outputRange
    background-color #fff
    padding 40px 30px 35px 20px
    margin 25px 25px 25px 0
    .errorMsg:nth-child(2)
      margin-top 40px
    .errorMsg
      margin 12.25px 0 0 0
      text-align left
      span > i
        color #FF0808
        font-size 1.3em
        margin 0 2px 0 0
      a
        font-size 1.4em
        color #525252

  .fade-enter-active, .fade-leave-active
    transition all .5s ease

  .fade-enter, .fade-leave-to
    transform translateX(-500px)
    opacity: 0;

  .divblock
    float right
</style>



